import React, { Component } from 'react';
import {requestArticle, requestUser} from "../../request";
import { Table, Divider, Icon, Button, Modal, Form, Input, Popconfirm, Select  } from 'antd';

const FormItem = Form.Item;
const { Option } = Select;

@Form.create()
class ArticleType extends Component{

  state = {
    users: [],
    visible: false,
    loading: false,
    method: 'post',
    id: ''
  };

  componentDidMount() {
    this.getUsers();
  }

  getUsers() {
    requestUser.get()
      .then(res => {
        this.setState({
          users: res.data.results
        });
      })
  }

  edit(record){
    this.props.form.setFieldsValue({
      name: record.name,
      account: record.account,
      role: record.role,
    });
    this.setState({
      visible: true,
      method: 'patch',
      id: record._id
    });
  };


  addType = () => {
    this.setState({
      visible: true,
      method: 'post'
    });
    this.props.form.setFieldsValue({
      name: "",
      account: "",
      role: "user",
    });
  };


  delete = (record) => {
    requestUser.delete(record._id)
      .then(res => {
        this.getUsers();
      })
  };


  handleSubmit = (e) => {
    const { method, id } = this.state;
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        let request = null;
        if (method === "patch") {
          request = requestUser.patch(id, values);
        } else {
          request = requestUser.post(values);
        }
        request.then(res => {
          this.setState({
            visible: false
          });
          this.getUsers();
        })
      }
    });
  };

  handleCancel = () => {
    this.setState({ visible: false });
  };

  render() {
    const { users, visible, loading } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 4 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 18 },
      },
    };
    const columns = [
      {
        title: '账号',
        dataIndex: 'account'
      },
      {
        title: '姓名',
        dataIndex: 'name',
      },
      {
        title: '角色',
        dataIndex: 'role',
      },
      {
        title: '操作',
        dataIndex: 'action',
        render: (text, record) => (
          <span>
            <Icon type="edit" onClick={() => this.edit(record)} style={{ cursor: "pointer" }}/>
            <Divider type="vertical" />
             <Popconfirm title="确认删除该选项吗?" onConfirm={() => this.delete(record)} okText="是" cancelText="否">
               <Icon type="delete" style={{ cursor: "pointer" }}/>
             </Popconfirm>
          </span>
        ),
      }
    ];

    return (
      <div>
        <div style={{ marginBottom: '10px' }}>
          <Button type="primary" onClick={this.addType}>添加</Button>
        </div>
        <Table dataSource={users} columns={columns} rowKey="name" bordered rowKey="_id"/>

        <Modal
          visible={visible}
          title="表单"
          onOk={this.save}
          onCancel={this.handleCancel}
          footer={null}
        >
          <Form onSubmit={this.handleSubmit}>
            <FormItem
              {...formItemLayout}
              label="账号">
              {getFieldDecorator('account', {
                rules: [{ required: true, message: '请填写账号!' }],
              })(
                <Input placeholder="请填写账号" />
              )}
            </FormItem>
            <FormItem
              {...formItemLayout}
              label="姓名">
              {getFieldDecorator('name')(
                <Input placeholder="请填写姓名" />
              )}
            </FormItem>
            <FormItem
              {...formItemLayout}
              label="角色">
              {getFieldDecorator('role', {
                initialValue: "user",
                rules: [{ required: true, message: '请选择角色!' }],
              })(
                <Select>
                  <Option value="super">super</Option>
                  <Option value="user">user</Option>
                </Select>
              )}
            </FormItem>
            <FormItem
              wrapperCol={{
              xs: { span: 24, offset: 0 },
              sm: { span: 16, offset: 4 },
            }}>
              <Button type="primary" htmlType="submit" loading={loading}>
                提交
              </Button>
            </FormItem>
          </Form>
        </Modal>

      </div>
    );
  }
}

export default ArticleType;