import React, { Component } from "react";
import { requestAttachmentMenu, requestAttachment } from "../../request";
import { Card, Col, Row, Tooltip, message, Upload, Button, Icon, Modal  } from 'antd';

const { Meta } = Card;

class Picture extends Component {

  state = {
    pictures: [],
    visible: false
  };

  componentDidMount() {
    this.requestPictures();
  }

  async requestPictures() {
    return requestAttachmentMenu.get().then(res => {
      this.setState({
        pictures: res.data.results
      })
    })
  }

  copyUrl = (url) => {
    if (document.execCommand("copy")) {
      const input = document.createElement("input");
      input.style.position = "fixed";
      input.style.top = "0";
      input.style.zIndex = -1;
      document.body.appendChild(input);
      input.value = url;
      input.focus();
      input.setSelectionRange(0, input.value.length);
      document.execCommand("copy", true);
      document.body.removeChild(input);
      message.success(`Copied: ${url}`, 2);
    } else {
      message.warning( "浏览器不支持复制功能！", 2);
    }
  };

   delete(id) {
    requestAttachment.delete(id).then(async res => {
      await this.requestPictures();
      message.success(res.data.message, 2);
    })
  }

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = (e) => {
    this.setState({
      visible: false,
    });
  };

  handleCancel = (e) => {
    this.setState({
      visible: false,
    });
  };

  render() {
    const { pictures, visible } = this.state;
    const uploadProps = {
      name: 'file',
      action: '//140.143.244.56:9999/admin/attachment',
      headers: {
        Authorization: localStorage.getItem("token")
      },
      onChange:(info) => {
        if (info.file.status === 'done') {
          this.requestPictures();
          message.success(`${info.file.name} 图片上传成功。`);
        } else if (info.file.status === 'error') {
          message.error(`${info.file.name} 图片上传失败。`);
        }
      },
      beforeUpload: (file, fileList) => {
        const types = ["image/jpeg", "image/png", "image/svg", "image/gif"];
        if (types.includes(file.type)) {
          return true;
        } else {
          fileList.pop();
          message.error(`只允许传图片格式文件！`);
          return false;
        }
      }
    };
    return (
      <div>
        <div style={{ marginBottom: "15px" }}>
          <Button onClick={this.showModal}>
            <Icon type="upload" /> 上传
          </Button>
        </div>
        <div>
          <Row gutter={16}>
            {
              pictures.map(pic => (
                <Col span={4} key={pic._id}>
                  <Card
                    style={{ marginBottom: "10px"}}
                    hoverable
                    cover={<img alt={pic.name} src={pic.url} style={{height: "160px"}} />}
                    actions={[<Icon type="copy" onClick={() => this.copyUrl(pic.url)}/>, <Icon type="delete" onClick={() => this.delete(pic._id)}/>]}
                  >
                    <Tooltip title={pic.name}>
                      <Meta
                        title={pic.name}>
                      </Meta>
                    </Tooltip>
                  </Card>
                </Col>
              ))
            }
          </Row>
        </div>
        <Modal
          title="上传"
          visible={visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
        >
          <Upload {...uploadProps}>
            <Button>
              <Icon type="upload" /> 上传
            </Button>
          </Upload>
        </Modal>
      </div>
    )
  }
}

export default Picture;