import axios from "../utils/axios";
import { queryFormat } from "../utils";
import errAction from "./error";

const request = function(url) {
  return {
    get(query) {
      return new Promise((resolve, reject) => {
        const queryStr = queryFormat(query);
        axios.get(url + queryStr).then(res => {
          resolve(res);
        }).catch(err => {
          errAction(err);
          reject(err);
        })
      })
    },
    post(data) {
      return new Promise((resolve, reject) => {
        axios({
          method: 'post',
          data: data,
          url: url
        }).then(res => {
          resolve(res);
        }).catch(err => {
          errAction(err);
          reject(err);
        })
      })
    },
    patch(id, data){
      return new Promise((resolve, reject) => {
        axios({
          method: 'patch',
          data: data,
          url: `${url}/${id}`
        }).then(res => {
          resolve(res);
        }).catch(err => {
          errAction(err);
          reject(err);
        })
      })
    },
    findOne(id) {
      return new Promise((resolve, reject) => {
        axios.get(`${url}/${id}`).then(res => {
          resolve(res);
        }).catch(err => {
          errAction(err);
          reject(err);
        })
      })
    },
    delete(id) {
      return new Promise((resolve, reject) => {
        axios.delete(`${url}/${id}`).then(res => {
          resolve(res);
        }).catch(err => {
          errAction(err);
          reject(err);
        })
      })
    },
    init({ method, url, data }) {
      return new Promise((resolve, reject) => {
        axios({
          method: method,
          url: url,
          data: data
        }).then(res => {
          resolve(res);
        }).catch(err => {
          errAction(err);
          reject(err);
        })
      })
    }
  }
};

const typeUrl = "/admin/article-type";
const articleMenuUrl = "/admin/article-menu";
const articleUrl = "/admin/article";
const attachmentUrl = "/admin/attachment";
const attachmentMenuUrl = "/admin/attachment/list";
const userUrl = "/admin/user";

const requestType = request(typeUrl);
const requestArticle = request(articleUrl);
const requestArticleMenu = request(articleMenuUrl);
const requestAttachment = request(attachmentUrl);
const requestAttachmentMenu = request(attachmentMenuUrl);
const requestUser = request(userUrl);

export {
  requestType,
  requestArticle,
  requestArticleMenu,
  requestAttachment,
  requestAttachmentMenu,
  requestUser
}