import Article from '../views/Article/Article';
import ArticleType from '../views/Article/ArticleType';
import ArticleEdit from '../views/Article/ArticleEdit';
import Picture from '../views/Picture';
import User from '../views/User/User';

export const basicRoutes = [
  {
    path: '/article',
    component: Article,
    requireAuth: true,
    exact: true
  },
  {
    path: '/article/edit/:id?',
    component: ArticleEdit,
    requireAuth: true,
    exact: false
  },
  {
    path: '/article/type',
    component: ArticleType,
    requireAuth: true,
    exact: false
  },
  {
    path: '/pictures',
    component: Picture,
    requireAuth: true,
    exact: false
  },
  {
    path: '/user',
    component: User,
    requireAuth: true,
    exact: false
  }
];